<template>
    <universal-layout>
        <template v-slot:header>
            <div style="display: flex; justify-content: flex-end">
                <el-button>Excel导入</el-button>
                <el-button>Excel导出</el-button>
                <el-button @click="add">新增</el-button>
            </div>
        </template>
        <template v-slot:main>
            <el-table :data="tableData">
                <el-table-column prop="theNameOfTheCustomer" label="客户名称" align="center" />
                <el-table-column prop="tel" label="联系电话" align="center" />
                <el-table-column prop="salesman" label="业务员" align="center" />
                <el-table-column
                    prop="theLastTimeYouCommunicated"
                    label="上次沟通时间"
                    align="center"
                />
                <el-table-column label="操作" align="center">
                    <template #default="{ row }">
                        <el-icon><PhoneFilled /></el-icon>
                        <el-icon><Postcard /></el-icon>
                        <el-button @click="warning(row)">预警</el-button>
                        <el-button @click="edit(row)">编辑</el-button>
                        <el-button @click="deleteRow(row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </template>
        <template v-slot:footer>
            <!-- 分页 -->
            <el-pagination
                v-model:current-page="paginator.currentPage"
                v-model:page-size="paginator.pageSize"
                :page-sizes="[16, 26, 36, 46]"
                layout="->,total, sizes, prev, pager, next, jumper"
                :total="paginator.total"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
        /></template>
    </universal-layout>
    <!-- 编辑、新增弹窗 -->
    <dialog-edit-box
        :title="dialogTitle"
        :loading="dialogloading"
        :structure="structure"
        :rowData="editData"
        @canceEdit="canceEdit"
        @saveEdit="saveEdit"
    ></dialog-edit-box>
</template>

<script setup lang="ts">
import { ref, reactive } from 'vue'

// 表格数据
const tableData = reactive([
    {
        theNameOfTheCustomer: 'jdshf',
        tel: '123456789',
        salesman: '张三',
        theLastTimeYouCommunicated: '2021-01-01'
    }
])

// 编辑
const edit = (row: any) => {
    editData.value = row
    dialogloading.value = true
}
// 删除
const deleteRow = (row: any) => {
    alert('删除ID为' + row.id + '的数据')
}
// 预警
const warning = (row: any) => {
    console.log(row)
}
// 新增
const add = () => {
    editData.value = {}
    dialogTitle.value = '新增'
    dialogloading.value = true
}

// 弹窗标题
let dialogTitle = ref<'新增' | '编辑'>('新增')

// 弹窗展示
let dialogloading = ref(false)

// 弹窗结构
const structure = ref([
    {
        label: '客户名称',
        prop: 'theNameOfTheCustomer',
        placeholder: '请输入客户名称',
        required: true,
        type: 'input'
    },
    {
        label: '联系电话',
        prop: 'tel',
        placeholder: '请输入联系电话',
        required: true,
        type: 'input'
    },
    {
        label: '业务员',
        prop: 'salesman',
        placeholder: '请输入业务员',
        required: true,
        type: 'input'
    },
    {
        label: '上次沟通时间',
        prop: 'theLastTimeYouCommunicated',
        placeholder: '请输入上次沟通时间',
        required: true,
        type: 'date'
    }
])

// 编辑数据
const editData = ref({})

// 弹窗取消，关闭弹窗
const canceEdit = () => {
    dialogloading.value = false
}

// 弹窗确认，关闭弹窗
const saveEdit = (row: any) => {
    dialogloading.value = false
    window.alert(JSON.stringify(row))
}

// 分页
let paginator = ref({
    currentPage: 1,
    pageSize: 16,
    total: 100
})

// 切换分页大小
const handleSizeChange = (val: number) => {
    paginator.value.currentPage = val
    paginator.value.pageSize = val
}

// 切换分页
const handleCurrentChange = (val: number) => {
    paginator.value.currentPage = val
}
</script>
<style scoped lang="scss"></style>
